<template>
    <div>
      <div v-if="error" class="message is-danger">
        <div class="message-body">
          <strong>
            <i class="fa fa-exclamation-triangle"></i>
            GC
          </strong>
          <p v-text="error.message"></p>
        </div>
      </div>
      <div class="level" v-if="current">
        <div class="level-item has-text-centered">
          <div>
            <p class="heading"></p>
            <p v-text="current.count"></p>
          </div>
        </div>
        <div class="level-item has-text-centered">
          <div>
            <p class="heading"></p>
            <p v-text="`${current.total_time.asSeconds()}s`"></p>
          </div>
        </div>
        <div class="level-item has-text-centered">
          <div>
            <p class="heading"></p>
            <p v-text="`${current.max.asSeconds()}s`"></p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import subscribing from '@/mixins/subscribing'
  import {Observable} from '@/utils/rxjs'
  import {mapActions} from 'vuex'

  export default {
    mixins: [subscribing],
    props: {
      instanceId: String
    },
    data: () => ({
      hasLoaded: false,
      error: null,
      current: null
    }),
    methods: {
      ...mapActions('application', ['fetchMetricData']),
      async fetchMetrics() {
        const response = await this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.gc.pause'
        })
        const measurements = response.measurements.reduce(
          (current, measurement) => ({
            ...current,
            [measurement.statistic.toLowerCase()]: measurement.value
          }), {}
        )
        return {
          ...measurements,
          total_time: this.$moment.duration(Math.round(measurements.total_time * 1000)),
          max: this.$moment.duration(Math.round(measurements.max * 1000))
        }
      },
      createSubscription() {
        const vm = this
        return Observable.timer(0, 2500)
          .concatMap(this.fetchMetrics)
          .subscribe({
            next: data => {
              vm.hasLoaded = true
              vm.current = data
            },
            error: error => {
              vm.hasLoaded = true
              console.warn('Fetching GC metrics failed:', error)
              vm.error = error
            }
          })
      }
    }
  }
</script>



