<template>
  <div ref="threadsChart" class="echarts"></div>
</template>

<script>
  import prettyBytes from 'pretty-bytes'

  export default {
    props: {
      chartType: String,
      data: {
        type: Array,
        default: () => []
      }
    },
    data() {
      return {
        option: {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          color: ['#f5bc63', '#0d91ff', '#29ba9c'],
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            top: '10%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: []
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: 'LIVE',
              type: 'line',
              areaStyle: {
                color: '#f5bc63',
                opacity: 1
              },
              data: []
            },
            {
              name: 'DAEMON',
              type: 'line',
              areaStyle: {
                color: '#0d91ff',
                opacity: 1
              },
              data: []
            }
          ]
        }
      }
    },
    mounted() {
      this.drawChart()
    },
    methods: {
      prettyBytes,
      drawChart() {
        const myChart = this.$echarts.init(this.$refs['threadsChart'])
        this.data.forEach(data => {
          this.option.xAxis[0].data.push(this.$moment(data.timestamp).format('hh:mm:ss'))
          if (this.chartType && this.chartType === 'heap') {
            this.option.yAxis[0].axisLabel = {
              formatter: '{value} MB'
            }
            this.option.series[0].name = 'SIZE'
            this.option.series[1].name = 'USED'
            this.option.series[0].data.push(parseFloat(prettyBytes(data.committed)))
            this.option.series[1].data.push(parseFloat(prettyBytes(data.used)))
          } else if (this.chartType === 'nonheap') {
            this.option.yAxis[0].axisLabel = {
              formatter: '{value} MB'
            }
            this.option.series[0].name = 'SIZE'
            this.option.series[1].name = 'USED'
            this.option.series[0].data.push(parseFloat(prettyBytes(data.committed)))
            this.option.series[1].data.push(parseFloat(prettyBytes(data.used)))
            this.option.series.push({
              name: 'METASPACE',
              type: 'line',
              areaStyle: {
                color: '#29ba9c',
                opacity: 1
              },
              data: []
            })
            this.option.series[2].data.push(parseFloat(prettyBytes(data.metaspace)))
          } else {
            this.option.series[0].data.push(data.live)
            this.option.series[1].data.push(data.daemon)
          }
        })
        myChart.setOption(this.option)
      }
    },
    watch: {
      data: 'drawChart'
    }
  }
</script>



