<template>
  <div>
    <div v-if="error" class="message is-danger">
      <div class="message-body">
        <strong>
          <i class="fa fa-exclamation-triangle"></i>
          .
        </strong>
        <p v-text="error.message"></p>
      </div>
    </div>
    <div class="level memory-current" v-if="current">
      <div class="level-item has-text-centered" v-if="current.metaspace">
        <div>
          <p class="heading has-bullet has-bullet-primary">Metaspace</p>
          <p v-text="prettyBytes(current.metaspace)"></p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading has-bullet has-bullet-info"></p>
          <p v-text="prettyBytes(current.used)"></p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading has-bullet has-bullet-warning"></p>
          <p v-text="prettyBytes(current.committed)"></p>
        </div>
      </div>
      <div class="level-item has-text-centered" v-if="current.max >= 0">
        <div>
          <p class="heading"></p>
          <p v-text="prettyBytes(current.max)"></p>
        </div>
      </div>
    </div>
    <threads-chart v-if="chartData.length > 0" :chartType="chartType" :data="chartData" />
  </div>
</template>

<script>
  import subscribing from '@/mixins/subscribing'
  import {Observable} from '@/utils/rxjs'
  import prettyBytes from 'pretty-bytes'
  import {mapActions} from 'vuex'

  import threadsChart from './threads-chart'

  export default {
    props: {
      instanceId: String,
      type: {
        type: String,
        required: true
      }
    },
    mixins: [subscribing],
    components: {threadsChart},
    data: () => ({
      hasLoaded: false,
      error: null,
      current: null,
      chartData: []
    }),
    computed: {
      chartType() {
        return this.type
      },
      name() {
        switch (this.type) {
          case 'heap':
            return 'Heap'
          case 'nonheap':
            return 'Non heap'
          default:
            return this.type
        }
      }
    },
    activated() {
      this.chartData = []
    },
    methods: {
      ...mapActions('application', ['fetchMetricData']),
      prettyBytes,
      async fetchMetrics() {
        const responseMax = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.memory.max',
          tags: {area: this.type}
        })
        const responseUsed = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.memory.used',
          tags: {area: this.type}
        })
        const responeMetaspace = this.type === 'nonheap'
          ? this.fetchMetricData({
            id: this.instanceId,
            metric: 'jvm.memory.used',
            tags: {area: this.type, id: 'Metaspace'}
          })
          : null
        const responseCommitted = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.memory.committed',
          tags: {area: this.type}
        })
        return {
          max: (await responseMax).measurements[0].value,
          used: (await responseUsed).measurements[0].value,
          metaspace: responeMetaspace ? (await responeMetaspace).measurements[0].value : null,
          committed: (await responseCommitted).measurements[0].value
        }
      },
      createSubscription() {
        const vm = this
        return Observable.timer(0, 2500)
          .concatMap(this.fetchMetrics)
          .subscribe({
            next: data => {
              vm.hasLoaded = true
              vm.current = data
              vm.chartData.push({...data, timestamp: this.$moment.now().valueOf()})
            },
            error: error => {
              vm.hasLoaded = true
              console.warn('Fetching memory metrics failed:', error)
              vm.error = error
            }
          })
      }
    }
  }
</script>

<style lang="scss">
  .memory-current {
    margin-bottom: 0 !important;
  }
</style>



