<template>
  <div class="instance-main">
    <div class="detail-back">
      <a @click="$router.push('/internal/applications')"><i class="iconfont icon-fanhui"></i></a>
      <span v-text="$route.meta.title + '-' + $route.params.id"></span>
    </div>
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <div>
              <div class="content info" v-if="instanceInfo">
                <table class="table" v-if="!isEmptyInfo">
                  <tr v-for="(value, key) in instanceInfo" :key="key">
                    <td class="info__key" v-text="key"></td>
                    <td>
                      <div v-html="yaml(value)" class="yaml"></div>
                    </td>
                  </tr>
                </table>
                <p v-else class="is-muted"></p>
              </div>
            </div>
          </div>
        </div>
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <div class="content metadata" v-if="metadata">
              <table class="table" v-if="!isEmptyMetadata">
                <tr v-for="(value, key) in metadata" :key="key">
                  <td class="metadata__key" v-text="key"></td>
                  <td>
                    <div v-html="yaml(value)" class="yaml"></div>
                  </td>
                </tr>
              </table>
              <p v-else class="is-muted"></p>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <health-details v-if="health" name="" :health="health"/>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <details-cpu v-if="hasProcess" :instanceId="instanceId" />
          </div>
        </div>
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <details-gc v-if="hasGc" :instanceId="instanceId" />
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <details-threads v-if="hasThreads" :instanceId="instanceId" />
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
     <el-col :span="12">
       <div class="card">
         <header class="card-header"></header>
         <div class="card-content">
           <details-memory v-if="hasMemory" :instanceId="instanceId" type="heap"/>
         </div>
       </div>
     </el-col>
      <el-col :span="12">
        <div class="card">
          <header class="card-header"></header>
          <div class="card-content">
            <details-memory v-if="hasMemory" :instanceId="instanceId" type="nonheap"/>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import linkify from '../../utils/linkify'
  import yaml from 'yamljs'
  import prettyBytes from 'pretty-bytes'
  import {mapState, mapActions} from 'vuex'
  import healthDetails from './health-details'
  import detailsGc from './details-gc'
  import detailsThreads from './details-threads'
  import detailsMemory from './details-memory'
  import detailsCpu from './details-cpu'

  export default {
    name: 'HealthDetails',
    data() {
      return {
        error: null,
        metrics: [],
        name: 'Instance',
        instanceId: ''
      }
    },
    components: {
      healthDetails,
      detailsGc,
      detailsThreads,
      detailsMemory,
      detailsCpu
    },
    computed: {
      ...mapState({
        instanceInfo: state => state.application.instanceInfo,
        health: state => state.application.instanceHealth,
        instance: state => state.application.currentInstance
      }),
      isEmptyInfo() {
        return Object.keys(this.instanceInfo).length <= 0
      },
      isEmptyMetadata() {
        return Object.keys(this.metadata).length <= 0
      },
      metadata() {
        if (this.instance.registration) {
          return this.instance.registration.metadata
        }
      },
      hasCaches() {
        return this.hasMetric('cache.gets')
      },
      hasDatasources() {
        return this.hasMetric('data.source.active.connections')
      },
      hasGc() {
        return this.hasMetric('jvm.gc.pause')
      },
      hasMemory() {
        return this.hasMetric('jvm.memory.max')
      },
      hasProcess() {
        return this.hasMetric('process.uptime')
      },
      hasThreads() {
        return this.hasMetric('jvm.threads.live')
      }
    },
    activated() {
      const instanceId = this.$route.params.id
      this.instanceId = instanceId
      this.fetchMetricIndex()
      this.getCurrentInstance(instanceId)
      this.fetchInfo(instanceId)
      this.fetchHealth(instanceId)
    },
    methods: {
      ...mapActions('application', ['getCurrentInstance', 'fetchMetrics', 'fetchInfo', 'fetchHealth', 'fetchMetricData']),
      prettyBytes,
      hasMetric(metric) {
        return this.metrics.includes(metric)
      },
      async fetchMetricIndex() {
        this.error = null
        try {
          const res = await this.fetchMetrics(this.instanceId)
          this.metrics = res.names
        } catch (error) {
          console.warn('Fetching metric index failed:', error)
          this.error = error
        }
      },
      yaml(value) {
        return linkify(yaml.stringify(value, 2), {maxLength: 50})
      }
    }
  }
</script>

<style lang="scss">
  .instance-main {
    .level {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 16px;
      .has-text-centered {
        text-align: center;
      }
      .heading {
        display: block;
        font-size: 12px;
        letter-spacing: 1px;
        margin-bottom: 5px;
        text-transform: uppercase
      }
    }
    .table {
      width: 100%;
      td, th {
        border-bottom: 1px solid #dbdbdb;
        text-align: left;
        vertical-align: top;
        line-height: 24px;
        font-size: 16px;
        padding: 8px 12px;
      }
    }
    .metadata {
      overflow: auto;
      &__key {
        vertical-align: top;
      }
    }
    .yaml {
      white-space: pre;
      line-height: 24px;
    }
    .card {
      background-color: #fff;
      box-shadow: 0 2px 3px hsla(0, 0%, 4%, .1), 0 0 0 1px hsla(0, 0%, 4%, .1);
      color: #4a4a4a;
      max-width: 100%;
      position: relative;
      margin-bottom: 30px;
      .card-header {
        align-items: center;
        color: #363636;
        font-weight: 700;
        padding: 15px;
        box-shadow: 0 1px 2px hsla(0, 0%, 4%, .1);
      }
      .card-content {
        padding: 30px;
      }
    }
    td.health-details__nested {
      padding: 0 0 0 0.75em;
    }

    td.health-details__nested {
      border-bottom: 0;
    }

    .health-details__nested .table {
      margin-bottom: 0.5em;
    }

    .health-details__status {
      float: right;

      &--UP {
        color: #23d160;
      }

      &--RESTRICTED {
        color: #e6a23c;
      }

      &--OUT_OF_SERVICE,
      &--DOWN {
        color: #f56c6c;
      }

      &--UNKNOWN,
      &--OFFLINE {
        color: #666;
      }
    }
    .has-bullet:before {
      background: currentColor;
      content: "";
      width: .75em;
      height: .75em;
      display: inline-block;
      margin-right: .25em
    }
    .has-bullet.has-bullet-white:before {
      background-color: #fff
    }

    .has-bullet.has-bullet-black:before {
      background-color: #0a0a0a
    }

    .has-bullet.has-bullet-light:before {
      background-color: #f5f5f5
    }

    .has-bullet.has-bullet-dark:before {
      background-color: #363636
    }

    .has-bullet.has-bullet-primary:before {
      background-color: #42d3a5
    }

    .has-bullet.has-bullet-link:before {
      background-color: #3273dc
    }

    .has-bullet.has-bullet-info:before {
      background-color: #209cee
    }

    .has-bullet.has-bullet-success:before {
      background-color: #23d160
    }

    .has-bullet.has-bullet-warning:before {
      background-color: #ffdd57
    }

    .has-bullet.has-bullet-danger:before {
      background-color: #ff3860
    }
    .message.is-danger {
      background-color: #fff5f7
    }

    .message.is-danger .message-header {
      background-color: #ff3860;
      color: #fff
    }

    .message.is-danger .message-body {
      border-color: #ff3860;
      color: #cd0930
    }

    .message-header {
      align-items: center;
      background-color: #4a4a4a;
      border-radius: 4px 4px 0 0;
      color: #fff;
      display: flex;
      font-weight: 700;
      justify-content: space-between;
      line-height: 1.25;
      padding: .75em 1em;
      position: relative
    }

    .message-header .delete {
      flex-grow: 0;
      flex-shrink: 0;
      margin-left: .75em
    }

    .message-header+.message-body {
      border-width: 0;
      border-top-left-radius: 0;
      border-top-right-radius: 0
    }

    .message-body {
      border-color: #dbdbdb;
      border-radius: 4px;
      border-style: solid;
      border-width: 0 0 0 4px;
      color: #4a4a4a;
      padding: 1.25em 1.5em;
      margin-bottom: 24px;
      line-height: 24px;
    }

    .message-body code,.message-body pre {
      background-color: #fff
    }

    .message-body pre code {
      background-color: transparent
    }
    .echarts {
      width: 100%;
      display: inline-block;
      height: 320px;
    }
  }
</style>



