<template>
  <div class="details-threads">
    <div v-if="error" class="message is-danger">
      <div class="message-body">
        <strong>
          <i class="fa fa-exclamation-triangle"></i>
          
        </strong>
        <p v-text="error.message"></p>
      </div>
    </div>
    <div class="level threads-current" v-if="current">
      <div class="level-item has-text-centered">
        <div>
          <p class="heading has-bullet has-bullet-warning"></p>
          <p v-text="current.live"></p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading  has-bullet has-bullet-info"></p>
          <p v-text="current.daemon"></p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading"></p>
          <p v-text="current.peak"></p>
        </div>
      </div>
    </div>
    <threads-chart v-if="chartData.length > 0" :data="chartData" />
  </div>
</template>

<script>
  import subscribing from '@/mixins/subscribing'
  import {Observable} from '@/utils/rxjs'
  import {mapActions} from 'vuex'
  import threadsChart from './threads-chart'

  export default {
    props: {
      instanceId: String
    },
    mixins: [subscribing],
    components: {threadsChart},
    data: () => ({
      hasLoaded: false,
      error: null,
      current: null,
      chartData: []
    }),
    activated() {
      this.chartData = []
    },
    methods: {
      ...mapActions('application', ['fetchMetricData']),
      async fetchMetrics() {
        const responseLive = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.threads.live'
        })
        const responsePeak = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.threads.peak'
        })
        const responseDaemon = this.fetchMetricData({
          id: this.instanceId,
          metric: 'jvm.threads.daemon'
        })

        return {
          live: (await responseLive).measurements[0].value,
          peak: (await responsePeak).measurements[0].value,
          daemon: (await responseDaemon).measurements[0].value
        }
      },
      createSubscription() {
        const vm = this
        return Observable.timer(0, 2500)
          .concatMap(this.fetchMetrics)
          .subscribe({
            next: data => {
              vm.hasLoaded = true
              vm.current = data
              vm.chartData.push({...data, timestamp: this.$moment.now().valueOf()})
            },
            error: error => {
              vm.hasLoaded = true
              console.warn('Fetching threads metrics failed:', error)
              vm.error = error
            }
          })
      }
    }
  }
</script>

<style lang="scss">
  .details-threads {
    .threads-current {
      margin-bottom: 0 !important;
    }
  }
</style>



