<template>
  <div>
    <div class="level">
      <div class="level-item has-text-centered" v-if="pid">
        <div>
          <p class="heading"></p>
          <p v-text="pid"></p>
        </div>
      </div>
      <div class="level-item has-text-centered" v-if="uptime">
        <div>
          <p class="heading"></p>
          <p>
            <process-uptime :value="uptime"/>
          </p>
        </div>
      </div>
      <div class="level-item has-text-centered" v-if="processCpuLoad">
        <div>
          <p class="heading">CPU</p>
          <p v-text="processCpuLoad.toFixed(2)"></p>
        </div>
      </div>
      <div class="level-item has-text-centered" v-if="systemCpuLoad">
        <div>
          <p class="heading">CPU</p>
          <p v-text="systemCpuLoad.toFixed(2)"></p>
        </div>
      </div>
      <div class="level-item has-text-centered" v-if="systemCpuCount">
        <div>
          <p class="heading">CPU</p>
          <p v-text="systemCpuCount"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import subscribing from '@/mixins/subscribing'
  import {Observable} from '@/utils/rxjs'
  import prettyBytes from 'pretty-bytes'
  import {mapActions} from 'vuex'
  import processUptime from './process-uptime'

  export default {
    props: {
      instanceId: String
    },
    mixins: [subscribing],
    components: {processUptime},
    data() {
      return {
        systemCpuLoad: null,
        processCpuLoad: null,
        pid: null,
        uptime: null,
        systemCpuCount: null
      }
    },
    created() {
      this.fetchPid(this.instanceId)
      this.fetchUptime(this.instanceId)
      this.fetchCpuCount(this.instanceId)
    },
    methods: {
      ...mapActions('application', ['fetchMetricData', 'fetchEnv']),
      prettyBytes,
      async fetchPid(instanceId) {
        try {
          const response = await this.fetchEnv({id: instanceId, name: 'PID'})
          this.pid = response.property.value
        } catch (error) {
          console.warn('Fetching PID failed:', error)
        }
      },
      async fetchUptime(instanceId) {
        try {
          this.uptime = await this.fetchMetric({id: instanceId, metric: 'process.uptime'})
        } catch (error) {
          this.error = error
          console.warn('Fetching Uptime failed:', error)
        }
      },
      async fetchCpuCount(instanceId) {
        try {
          this.systemCpuCount = await this.fetchMetric({id: instanceId, metric: 'system.cpu.count'})
        } catch (error) {
          console.warn('Fetching Cpu Count failed:', error)
        }
      },
      async fetchCpuLoadMetrics() {
        const fetchProcessCpuLoad = this.fetchMetric({
          id: this.instanceId,
          metric: 'process.cpu.usage'
        })
        const fetchSystemCpuLoad = this.fetchMetric({
          id: this.instanceId,
          metric: 'system.cpu.usage'
        })
        let processCpuLoad
        let systemCpuLoad
        try {
          processCpuLoad = await fetchProcessCpuLoad
        } catch (error) {
          console.warn('Fetching Process CPU Load failed:', error)
        }
        try {
          systemCpuLoad = await fetchSystemCpuLoad
        } catch (error) {
          console.warn('Fetching Sytem CPU Load failed:', error)
        }
        return {
          processCpuLoad,
          systemCpuLoad
        }
      },
      async fetchMetric(data) {
        const response = await this.fetchMetricData(data)
        return response.measurements[0].value
      },
      createSubscription() {
        const vm = this
        return Observable.timer(0, 2500)
          .concatMap(this.fetchCpuLoadMetrics)
          .subscribe({
            next: data => {
              vm.processCpuLoad = data.processCpuLoad
              vm.systemCpuLoad = data.systemCpuLoad
            },
            error: error => {
              vm.hasLoaded = true
              console.warn('Fetching CPU Usage metrics failed:', error)
              vm.error = error
            }
          })
      }
    }
  }
</script>



