export default {
  activated() {
    this.subscribe()
  },
  created() {
    this.subscribe()
  },
  beforeDestroy() {
    this.unsubscribe()
  },
  deactivated() {
    this.unsubscribe()
  },
  methods: {
    async subscribe() {
      if (!this.subscription) {
        this.subscription = await this.createSubscription()
      }
    },
    unsubscribe() {
      if (this.subscription) {
        try {
          !this.subscription.closed && this.subscription.unsubscribe()
        } finally {
          this.subscription = null
        }
      }
    }
  }
}



